
.exampleLayouts_container {
  margin: 0 120px 70px 120px;
}

@media (max-width: 991px) {
  .exampleLayouts_container {
    margin: 0 60px 70px 60px;
  }
}

@media (max-width: 565px) {
  .exampleLayouts_container {
    margin: 0 30px 70px 30px;
  }
}

@media (max-width: 320px) {
  .exampleLayouts_container {
    margin: 40px 15px;
  }
}

.exampleLayouts_wrapper {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-row-gap: 20px;
  grid-column-gap: 20px;
}

@media (max-width: 565px) {
  .exampleLayouts_wrapper {
    display: grid;
    grid-row-gap: 25px;
    grid-column-gap: 15px;
    grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
  }
}

.checkBox {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  margin: 0 0 30px 120px;
}

@media (max-width: 991px) {
  .checkBox {
    margin: 0 0 30px 60px;
  }
}

@media (max-width: 565px) {
  .checkBox {
    margin: 0 0 30px 30px;
  }
}

.checkBoxContainer {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  cursor: pointer;
  font-size: 22px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  font: var(--old-standart-25-35);
  color: var(--core-dark-color);
}

.checkBoxContainer.light {
  color: var(--core-light-color);
}

@media (max-width: 400px) {
  .checkBoxContainer,
  .checkBoxContainer.light {
    font: var(--old-standart-15-25);
  }
}

/* Hide the browser's default checkbox */
.checkBoxContainer input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

/* Create a custom checkbox */
.checkmark {
  position: absolute;
  top: 6px;
  left: 0;
  height: 20px;
  width: 20px;
  background-color: var(--checkmark-bg-color);
}

@media (max-width: 400px) {
  .checkmark {
  top: 0;
  }
}

@media (max-width: 320px) {
  .checkmark {
  top: 3px;
  }
}

/* On mouse-over, add a grey background color */
.checkBoxContainer:hover input ~ .checkmark {
  background-color: var(--medium-gray);
}
.checkBoxContainer:hover input ~ .checkmark {
  background-color: var(--medium-gray);
}

/* When the checkbox is checked, add a blue background */
.checkBoxContainer input:checked ~ .checkmark {
  background-color:  var(--yellow-color);
}
.checkBoxContainer input:checked ~ .checkmark {
  background-color:  var(--yellow-color);
}

/* Create the checkmark/indicator (hidden when not checked) */
.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the checkmark when checked */
.checkBoxContainer input:checked ~ .checkmark:after {
  display: block;
}
.checkBoxContainer input:checked ~ .checkmark:after {
  display: block;
}

/* Style the checkmark/indicator */
.checkBoxContainer .checkmark:after {
  left: 6px;
  top: 1px;
  width: 8px;
  height: 15px;
  border: solid var(--white);
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}
.checkBoxContainer .checkmark:after {
  left: 6px;
  top: 1px;
  width: 8px;
  height: 15px;
  border: solid var(--white);
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}

.layoutsItems {
  line-height: 0.5;
  background-color: var(--bg-color);
  border: 1px var(--border-color);
  box-shadow: inset 0 4px 5px -4px var(--shadow-color-1),
  inset 0 -31px 34px -32px var(--shadow-color-2),
  inset 0 39px 50px -34px var(--shadow-color-3),
  inset 0 2px 9px var(--shadow-color-4),
  inset 0 1px 10px var(--shadow-color-5);
  backdrop-filter: blur(100px);
  padding: 3.5rem 1.5rem 2rem;
  border-radius: 1rem;
  -webkit-transition: 0.3s;
  -moz-transition: 0.3s;
  transition: 0.3s;
  cursor: grab;
}
.layoutsItems:hover {
  box-shadow: inset 0 4px 5px -4px var(--hover-shadow-color-1),
  inset 0 -31px 34px -32px var(--hover-shadow-color-2),
  inset 0 39px 50px -34px var(--hover-shadow-color-3),
  inset 0 2px 9px var(--hover-shadow-color-4),
  inset 0 1px 10px var(--hover-shadow-color-5);
}
@media (max-width: 991px){
  .layoutsItems:hover {
    box-shadow: none;
  }
}

.layoutsItems_refImg {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: center;
  font: var(--old-standart-13-23);
  color: var(--hover-shadow-color-1);
  letter-spacing: 2px;
  text-transform: uppercase;
  margin: 20px 0 41px 0;
}
.layoutsItems img {
  display: block;
  width: 100%;
  padding: 10% 10% 0 10%;
  filter: drop-shadow(0px 8px 24px rgba(0, 0, 0, 0.6));
}

.selected {
  line-height: 0.5;
  background-color: var(--selected-bg-color);
  border: 1px solid var(--border-color);
  box-shadow: inset 0 4px 5px -4px var(--shadow-color-1), inset 0 -31px 34px -32px var(--shadow-color-2), inset 0 39px 50px -34px var(--shadow-color-3), inset 0 2px 9px var(--shadow-color-4), inset 0 1px 10px var(--shadow-color-5);
  backdrop-filter: blur(100px);
  padding: 3.5rem 1.5rem 2rem;
  border-radius: 1rem;
  -webkit-transition: 0.3s;
  -moz-transition: 0.3s;
  transition: 0.3s;
  cursor: grab;
}